@keyframes gradient {
  70% {
    transform: scale(1.5) translate(50px);
  }
}

.gradient-background-v2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;

  div:nth-child(2) {
    position: absolute;
    border-radius: 56%;
    height: 40%;
    width: 30%;
    top: 25%;
    left: 30%;
    background-color: map.get($colors, "primary-100");
    animation: move-left-to-right 10s infinite;
  }

  div:nth-child(1) {
    position: absolute;
    border-radius: 56%;
    height: 40%;
    width: 30%;
    top: 25%;
    left: 15%;
    background-color: map.get($colors, "secondary-100");
    animation: move-left-to-right 10s infinite;
    animation-delay: 0.5s;
  }

  &::after {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    backdrop-filter: blur(80px);
  }
}

@keyframes move-left-to-right {
  0% {
    left: 10%;
  }
  50% {
    left: 60%;
  }
  100% {
    left: 10%;
  }
}


